<template>
	<div>
		<NavBar>
			<div slot='left' class="back" @click='backClick'>
				<img src="@/assets/img/common/back.svg">
			</div>
			<div slot='center' class="title">
				<div v-for="(title, index) in titles" 
						:key='index'
						class="title-item"
						:class="{active: index === currentIndex}"
						@click="titleClick(index)"
						>{{title}}
				</div>
			</div>
		</NavBar>
	</div>
</template>

<script>
	import NavBar from '@/components/common/navbar/NavBar.vue'
	
	export default {
		components: {
			NavBar
		},
		data() {
			return {
				titles: ['商品', '参数', '评论', '推荐'],
				currentIndex: 0
			}
		},
		methods:{
			titleClick(index) {
				this.currentIndex = index;
				this.$emit('titleClick', index)
			},
			backClick() {
				this.$router.back()
			}
		}
	}
</script>

<style scoped>
	.title {
		display: flex;
		font-size: 13px;
	}
	.title-item {
		flex: 1;
	}
	.active {
		color: val(--color-high-text);
	}
	.back img {
		margin-top: 12px;
	}
</style>
